<template>
  <h1>
    I know how to use
    <span id="text">{{ currentSkillText }}</span>
    <span id="cursor" :class="{ 'bg-dark': !night, 'bg-light': night }"></span>
  </h1>
</template>

<script>
export default {
  props: ["night"],
  data() {
    return {
      skills: [
        "JavaScript",
        "C++",
        "C",
        "HTML5",
        "CSS",
        "C#",
        "Typescript",
        "Powershell",
        "Swift",
        "Express",
        "Ionic",
        "AngularJS",
        "jQuery",
        "Bootstrap",
        "Koa",
        "React.js",
        "React Native",
        "GraphQL",
        "Node.js",
        "Git",
        "Docker",
        "MySQL",
        "MongoDB",
        "Linux",
        "Mac OS",
        "Arduino",
        "Raspberry Pi",
        "Bash",
        "Unity",
        "StackOverflow",
        "Copy/Paste"
      ],
      skillsIndex: 0,
      skillsLetterIndex: 0,
      currentSkillText: "",
      interval: undefined
    };
  },
  methods: {
    delete: function () {
      this.interval = setInterval(() => {
        this.currentSkillText = this.skills[this.skillsIndex].substring(
          0,
          this.skillsLetterIndex - 1
        );
        this.skillsLetterIndex -= 1;
        if (this.currentSkillText === "") {
          this.skillsIndex = (this.skillsIndex + 1) % this.skills.length;
          clearInterval(this.interval);
          this.type();
        }
      }, 100);
    },
    type: function () {
      this.interval = setInterval(() => {
        this.currentSkillText = this.skills[this.skillsIndex].substring(
          0,
          this.skillsLetterIndex + 1
        );
        this.skillsLetterIndex += 1;
        if (this.currentSkillText === this.skills[this.skillsIndex]) {
          clearInterval(this.interval);
          setTimeout(this.delete, 1250);
        }
      }, 200);
    }
  },
  mounted() {
    this.$nextTick(function () {
      this.type();
    });
  }
};
</script>

<style scoped>
p {
  text-align: justify;
  font-weight: 500;
}

#text {
  display: inline-block;
  /* vertical-align: middle; */
  letter-spacing: 2px;
}

#cursor {
  display: inline-block;
  vertical-align: middle;
  width: 3px;
  height: 40px;
  margin-top: -5px;
  animation: blink 0.75s step-end infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}
</style>
